import React from 'react';
import PropTypes from 'prop-types';
import { Tooltip, Button } from 'antd';

import MSTRIcon from 'components/common/icons/MSTRIcon';
import Checkbox from 'components/common/Checkbox';
import CategoryBox from './CategoryBox';
import TextToInput from './TextToInput';

import './DetailsBox.scss';

export default function DetailsBox({ dfName, objects, hideUnselected = false, onBoxChoose = () => {}, nameValidator = () => {}, dfNameSetter = () => {}, objNameSetter = () => {}, onObjsMove = () => {}, onObjsSelect = () => {}, t = (text) => text }) {
  let attributes = [];
  let metrics = [];
  const icons = {
    attribute: <MSTRIcon type="attribute" />,
    metric: <MSTRIcon type="metric" />,
  };

  objects.forEach((obj) => {
    if (obj.type === 'attribute') {
      attributes.push(obj);
    } else { // if obj.type === 'metric'
      metrics.push(obj);
    }
  });

  if (hideUnselected) {
    metrics = metrics.filter(({ isSelected }) => isSelected);
    attributes = attributes.filter(({ isSelected }) => isSelected);
  }

  const selectAllSelected = hideUnselected || !objects.some(({ isSelected }) => !isSelected);

  const selectAllHandler = () => {
    const allObjsToApplyOn = objects.filter(({ isSelected }) => isSelected === selectAllSelected).map(({ name }) => name);
    onObjsSelect(dfName, allObjsToApplyOn);
  };

  const moveObjectsHandler = (target) => {
    const source = target === 'attributes' ? metrics : attributes;
    const allObjsToApplyOn = source.filter(({ isSelected }) => isSelected).map(({ name }) => name);
    onObjsMove(dfName, allObjsToApplyOn, target);
  };

  return (
    <div className="details-box">
      <div className="box-title-container" title={dfName}>
        <Checkbox
          checked={selectAllSelected}
          id={`${dfName}-select-all-absolute`}
          onChange={() => { selectAllHandler(); }}
        />
        <span><TextToInput value={dfName} validator={nameValidator} setter={dfNameSetter} /></span>
      </div>
      <CategoryBox
        title="Attributes"
        dfName={dfName}
        objects={attributes}
        icon={icons.attribute}
        nameValidator={nameValidator}
        objNameSetter={objNameSetter}
        onObjsSelect={onObjsSelect}
        t={t}
      />
      <div className="arrow-holder">
        <Tooltip placement="left" trigger="hover" mouseEnterDelay={0.2} title={t('Convert selected Attributes to Metrics')}>
          <Button id={`${dfName}-button-attributes-to-metrics`} onClick={() => { moveObjectsHandler('metrics'); }}>&rsaquo;</Button>
        </Tooltip>
        <Tooltip placement="right" trigger="hover" mouseEnterDelay={0.2} title={t('Convert selected Metrics to Attributes')}>
          <Button id={`${dfName}-button-metrics-to-attributes`} onClick={() => { moveObjectsHandler('attributes'); }}>&lsaquo;</Button>
        </Tooltip>
      </div>
      <CategoryBox
        title="Metrics"
        dfName={dfName}
        objects={metrics}
        icon={icons.metric}
        nameValidator={nameValidator}
        objNameSetter={objNameSetter}
        onObjsSelect={onObjsSelect}
        t={t}
      />
    </div>
  );
}

DetailsBox.propTypes = {
  dfName: PropTypes.string.isRequired,
  objects: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    type: PropTypes.string,
    isSelected: PropTypes.bool,
  })).isRequired,
  hideUnselected: PropTypes.bool,
  onBoxChoose: PropTypes.func,
  nameValidator: PropTypes.func,
  dfNameSetter: PropTypes.func,
  objNameSetter: PropTypes.func,
  onObjsMove: PropTypes.func,
  onObjsSelect: PropTypes.func,
  t: PropTypes.func,
};
