import React, { useState } from 'react';
import PropTypes from 'prop-types';

import './TextToInput.scss';

export default function TextToInput({ value, validator, setter }) {
  const [isInputShown, setIsInputShown] = useState(false);
  const [inputValue, setInputValue] = React.useState(value);

  const toggle = () => {
    setIsInputShown((state) => !state);
  };

  const onInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const onBlur = (event) => {
    if (value === inputValue) {
      toggle();
      return;
    }

    const isValid = validator(inputValue);
    if (isValid) {
      setter(value, inputValue);
      toggle();
    } else {
      event.target.focus();
    }
  };

  const onInputKeyDown = (event) => {
    switch (event.key) {
      case 'Enter':
        onBlur(event);
        break;
      default:
        break;
    }
  };

  const onButtonKeyDown = (event) => {
    switch (event.key) {
      case 'Enter':
        toggle();
        break;
      default:
        break;
    }
  };

  if (isInputShown) {
    // eslint-disable-next-line jsx-a11y/no-autofocus
    return <input className="text-to-input-field" type="text" value={inputValue} onChange={onInputChange} onBlur={onBlur} onKeyDown={onInputKeyDown} autoFocus />;
  }

  return <button className="text-to-input-button" type="button" onDoubleClick={toggle} onKeyDown={onButtonKeyDown}>{value}</button>;
}

TextToInput.propTypes = {
  value: PropTypes.string.isRequired,
  validator: PropTypes.func,
  setter: PropTypes.func,
};
