import React from 'react';
import PropTypes from 'prop-types';
import { Checkbox } from 'antd';
import TextToInput from './TextToInput';

import './DataframesSelection.scss';

export default function DataframesSelection({ dfNames, selectedDfs = [], onSelect = () => {}, dfNameValidator = () => {}, dfNameSetter = () => {}, t = (text) => text }) {
  return (
    <div className="dataframes-selection-container">
      <h2>{t('Available Dataframes')}</h2>
      <ul className="dataframes-selection-list">
        {
          dfNames.map((dfName) => {
            const checked = selectedDfs.includes(dfName);
            const checkboxId = `checkbox-${dfName}`;
            return (
              <li key={dfName} title={dfName}>
                <Checkbox onChange={() => onSelect(dfName)} checked={checked} id={checkboxId} />
                <TextToInput value={dfName} validator={dfNameValidator} setter={dfNameSetter} />
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}

DataframesSelection.propTypes = {
  dfNames: PropTypes.arrayOf(PropTypes.string).isRequired,
  selectedDfs: PropTypes.arrayOf(PropTypes.string),
  onSelect: PropTypes.func,
  dfNameValidator: PropTypes.func,
  dfNameSetter: PropTypes.func,
  t: PropTypes.func,
};
