import React from 'react';
import { Tooltip } from 'antd';
import './Header.scss';
import MSTRIcon from 'components/common/icons/MSTRIcon';
import { disableFocusOnMouseClick } from 'utilities/Utilities';
import { modes } from 'StateContext';
import UserTooltip from './UserTooltip';

export default function Header({ visible, title, t, changeMode, mode }) {
  React.useEffect(() => {
    disableFocusOnMouseClick('.top-nav li a');
  }, []);

  const showTopNav = mode === modes.IMPORT.MAIN || mode === modes.EXPORT.MAIN;

  if (visible) {
    return (
      <div className="header">
        <div className="left-side">
          <div className="logo" tabIndex="-1">
            <Tooltip placement="right" title={t(title)}>
              <div className="mstr-logo-container">
                <MSTRIcon type="mstr-logo" />
              </div>
            </Tooltip>
          </div>
          { showTopNav
          && (
          <div className="top-nav">
            <ul>
              <li><button onClick={() => changeMode(modes.IMPORT.MAIN)} className={mode === modes.IMPORT.MAIN ? 'active' : 'inactive'} id="import-data-tab-button" type="button">{t('Import Data')}</button></li>
              <li><button onClick={() => changeMode(modes.EXPORT.MAIN)} className={mode.startsWith('export') ? 'active' : 'inactive'} id="export-data-tab-button" type="button">{t('Export Data')}</button></li>
            </ul>
          </div>
          )}
        </div>
        <UserTooltip />
      </div>
    );
  }
  return null;
}
