/* eslint-disable react/no-array-index-key */
import React from 'react';
import PropTypes from 'prop-types';
import { modes } from 'StateContext';

import './StepIndicator.scss';

export default function StepIndicator({ mode = modes.EXPORT.CREATE.DATA_MODELING, t = (text) => text }) {
  const stepNames = [];
  stepNames[0] = t('Data Modeling');

  if (mode.startsWith('export-update')) {
    stepNames[1] = t('Update Policy Selection');
    stepNames[2] = t('Data Publishing');
  } else {
    stepNames[1] = t('Data Publishing');
  }

  let currentStep;

  switch (mode) {
    case modes.EXPORT.CREATE.DATA_MODELING:
    case modes.EXPORT.UPDATE.DATA_MODELING:
      currentStep = 0;
      break;
    case modes.EXPORT.CREATE.DIRECTORY_SELECTION:
    case modes.EXPORT.UPDATE.POLICY_SELECTION:
      currentStep = 1;
      break;
    case modes.EXPORT.UPDATE.DATA_PUBLISHING:
      currentStep = 2;
      break;
    default:
      currentStep = 0;
  }


  const elements = stepNames.map((stepName, index) => {
    const separator = index > 0 ? <div className="separator">&#9654;</div> : null;
    return (
      <React.Fragment key={`step-${index}`}>
        {separator}
        <div className={`step-name step-${index} ${index === currentStep && 'current'}`}>
          {stepName}
        </div>
      </React.Fragment>
    );
  });

  return (
    <div className="step-indicator">
      {elements}
    </div>
  );
}

StepIndicator.propTypes = {
  mode: PropTypes.string,
  t: PropTypes.func,
};
