import React from 'react';
import { Input } from 'antd';

import './SearchBar.scss';
import { modes } from 'StateContext';

export default function SearchBar({ t, mode, onSearch, searchText }) {
  let title = '';
  switch (mode) {
    case modes.IMPORT.MAIN:
      title = 'Object List';
      break;
    case modes.EXPORT.MAIN:
      title = 'Cube List';
      break;
    case modes.PROJECT_SELECTION:
      title = 'Select Applications';
      break;
    default:
      title = '';
  }

  return (
    <div className="search-bar">
      <h2>{t(title)}</h2>
      <div className="object-search">
        <Input.Search
          placeholder={`${t('Search')}...`}
          onChange={onSearch}
          className="project-chooser-search"
          value={searchText}
        />
      </div>
    </div>
  );
}
