import React from 'react';
import { Input } from 'antd';
import ViewSelectedSwitch from 'components/common/ViewSelectedSwitch';
import StepIndicator from './StepIndicator';

import './TopBar.scss';

export default function TopBar({ mode, t, onSearch, searchText, onViewSelectedChange, viewSelected }) {
  return (
    <div className="top-bar search-bar">
      <div className="left">
        <StepIndicator mode={mode} t={t} />
      </div>
      <div className="right">
        <ViewSelectedSwitch t={t} onChange={onViewSelectedChange} checked={viewSelected} />
        <div className="object-search">
          <Input.Search
            placeholder={`${t('Search')}...`}
            onChange={onSearch}
            className="project-chooser-search"
            value={searchText}
          />
        </div>
      </div>
    </div>
  );
}
