import React, { Component } from 'react';

import { backend } from 'utilities/BackendHelper';
import Header from 'components/common/Header';
import StateContext, { modes } from 'StateContext';

// eslint-disable-next-line react/prefer-stateless-function
export default class MainContainer extends Component {
  changeMode = (mode) => {
    const { updateStateContext } = this.context;
    if (mode === modes.EXPORT.MAIN) {
      updateStateContext({ reloadDataSource: false });
    }
    updateStateContext({ mode });
  }

  render() {
    const { t, api, mode } = this.context;
    const { children } = this.props;
    const title = t('MicroStrategy for {{programName}}', { programName: backend });
    return (
      <div className="content" mode={mode}>
        <Header title={title} visible={api.isAuthenticated} t={t} changeMode={this.changeMode} mode={mode} />
        {children}
      </div>
    );
  }
}

MainContainer.contextType = StateContext;
