import React from 'react';
import { Switch } from 'antd';

import './ViewSelectedSwitch.scss';

export default function ViewSelectedSwitch({ checked, t, onChange }) {
  return (
    <div className="view-selected-switch">
      <label className="switch-label" htmlFor="view-selected-switch">{t('View selected')}</label>
      <Switch checked={checked} id="view-selected-switch" onChange={onChange} />
    </div>
  );
}
