import React, { Component } from 'react';
import { Button, Tooltip } from 'antd';
import { FolderBrowser } from 'components/object-table/folder-browser';

import backendHelper from 'utilities/BackendHelper';
import Footer from 'components/common/Footer';
import MainContainer from 'components/common/MainContainer';
import StateContext, { modes } from 'StateContext';


export default class ExportData extends Component {
  constructor() {
    super();

    this.state = {
      searchText: '',
    };
  }

  componentDidMount() {
    const { updateStateContext } = this.context;
    document.addEventListener('keydown', this.handleKeyDown);
    updateStateContext({ selectedCubeToUpdate: {} });
    backendHelper.onExportDataMode();
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown);
  }

    handleKeyDown = (e) => {
      const activatedElements = [...document.querySelectorAll('button, a, [tabindex="0"], input[type="checkbox"], input[type="text"]')];
      const hasDefaultEnterKeyAction = activatedElements.includes(e.target);
      if (e.keyCode === 13 && !hasDefaultEnterKeyAction) {
        document.querySelector('#prepare').click();
      }
    }

    handleBack = () => {
      const { updateStateContext } = this.context;
      updateStateContext({
        mode: modes.IMPORT.MAIN,
      });
    }

    onObjectChosen = async (objectId, projectId, subtype, name, projectName) => {
      const { updateStateContext } = this.context;
      const selectedCubeToUpdate = { projectId, projectName, objectId, subtype, name };
      updateStateContext({ selectedCubeToUpdate });
    }

    onSearchChange = (searchText) => {
      this.setState({ searchText });
    }

    handleCreate = () => {
      this.context.updateStateContext({ mode: modes.EXPORT.CREATE.DATA_MODELING });
    }

    handleUpdate = () => {
      this.context.updateStateContext({ mode: modes.EXPORT.UPDATE.DATA_MODELING });
    }

    render() {
      const { api, t, selectedCubeToUpdate, selectedProjects } = this.context;
      const { searchText } = this.state;
      const session = {
        USE_PROXY: false,
        url: `${api.envUrl}api`,
        authToken: api.token,
      };

      const isSomethingSelected = Object.entries(selectedCubeToUpdate).length !== 0;
      // eslint-disable-next-line no-nested-ternary
      const updateButtonTooltipTitle = isSomethingSelected ? null : 'This button is disabled because you didn\'t select any cube to update';
      return (
        <MainContainer>
          <FolderBrowser
            api={api}
            session={session}
            onObjectChosen={this.onObjectChosen}
            projectIds={selectedProjects}
            onSearchChange={this.onSearchChange}
            searchText={searchText}
            handleBack={this.handleBack}
            t={t}
            // chosenItem={savedRecord}
          />
          <Footer
            left={<Button id="back" onClick={this.handleBack}>{t('Back')}</Button>}
            right={(
              <div>
                <Tooltip placement="top" title={updateButtonTooltipTitle} mouseEnterDelay={1} overlayClassName="info-tooltip">
                  <Button id="update" onClick={this.handleUpdate} disabled={!isSomethingSelected}>{t('Update')}</Button>
                </Tooltip>
                <Tooltip placement="topRight" mouseEnterDelay={1} overlayClassName="info-tooltip">
                  <Button id="create" type="primary" onClick={this.handleCreate}>{t('Create')}</Button>
                </Tooltip>
              </div>
            )}
          />
        </MainContainer>
      );
    }
}

ExportData.contextType = StateContext;
